.container {
    border: 1px solid #FFFFFF;
    background: #fff;
    border-radius: 12px;
    padding: 24px 24px 20px;
}
.input-container {
    height: 44px;
    border: 2px solid #EAEEFC;
    padding-left: 10px;
    border-radius: 8px;
    background: linear-gradient(90deg, #F8FAFF 23%, rgba(255, 255, 255, 0.6) 96%);
}
.input {
    width: 100%;
}
.icon {
    font-size: 24px;
    color: #3b3b3b;
    margin: 0 6px;
}
.ant-select-selector {
    background: transparent !important;
    border: none !important;
    box-shadow: none !important;
}
.ant-select-arrow {
    display: none !important;
}
p {
    font-size: 14px;
    color: #70749E;
    margin-top: 20px;
}
.history-tag {
    border: 1px solid #EAEEFC;
    border-radius: 8px;
    background: linear-gradient(90deg, #F8FAFF 23%, rgba(255, 255, 255, 0.6) 96%);
    color: #70749E;
    padding: 6px 12px;
    cursor: pointer;
}
.history-tag-name {
    padding-left: 12px;
    color: #020C1A;
}

/* override ant */
.ant-input-group-addon {
    background: transparent !important;
    border: none !important;
    font-size: 24px !important;
}
.ant-input {
    border: none !important;
    background: transparent !important;
}
.ant-input:focus {
    box-shadow: none;
}
.ant-input-group-wrapper {
    padding-top: 3px !important;
}